import React from "react";
import pravin from "../../styles/assets/images/Teammates/Pravin Raj.png";
import vijay from "../../styles/assets/images/Teammates/Vijay.png";
import prasanna from "../../styles/assets/images/Teammates/Prasanna.png";
import kartick from "../../styles/assets/images/Teammates/Karthick.png";
import sudhar from "../../styles/assets/images/Teammates/Sudharsan.png";
import anbu from "../../styles/assets/images/Teammates/Anbu.png";
import { Swiper, SwiperSlide } from "swiper/react";
import {
  Navigation,
  Scrollbar,
  A11y,
  EffectFade,
  Autoplay,
} from "swiper/modules";
import { BreakPoints } from "../../core/const";

const ourTeams = [
  {
    name: "Pravin Raj",
    role: "Full Stack Developer",
    description:
      "As a fullstack developer, He has 8 years of experience in building and maintaining web applications. His skills in languages such as HTML, CSS, JavaScript, and Php, as well as frameworks such as React, Angular, NextJs, ExpressJs and NestJS, created efficient and user-friendly applications from start to finish. He design, develop platforms with databases or servers.",
    profileImg: pravin,
  },
  {
    name: "Sudharsan",
    role: "UI/UX Designer",
    description:
      "He is implementing design and interaction guidelines to enhance usability and accessibility across devices and platforms, staying updated with industry trends and emerging technologies to continuously enhance UI/UX practices, and Communicating effectively within cross-functional teams to deliver cohesive and user-friendly digital products.",
    profileImg: sudhar,
  },
  {
    name: "Vijay",
    role: "Front End Developer",
    description:
      "As a front-end developer, His passion for building beautiful and intuitive user experiences. With a strong foundation in HTML, CSS, and JavaScript, He delivers high-quality web projects on time and on budget. He always looking for new challenges and opportunities to improve skills and contribute to the success of his team and clients.",
    profileImg: vijay,
  },
  {
    name: "Prasanna",
    role: "Back End Developer",
    description:
      "As a backend developer, He specialized in building and maintaining efficient and scalable server-side systems. With experience in languages such as JavaScript and Php, as well as databases like MySql and MongoDb. He skilled at integrating with various APIs and databases to create seamless and user-friendly web applications.",
    profileImg: prasanna,
  },

  {
    name: "Anbarasan",
    role: "QA Engineer",
    description:
      "Ensuring the quality and functionality of software products by conducting through testing and identifying defects. They create test plans, execute test cases, and document findings. Also collaborate with development teams to communicate issues, verify bug fixes, and ensure the software meets quality standards before release.",
    profileImg: anbu,
  },
  {
    name: "Karthick",
    role: "UI Developer",
    description:
      "Designs and creates user interfaces for digital products, ensuring they are visually appealing, intuitive, and responsive. They collaborate with UX designers and front-end developers to translate design concepts into code using HTML, CSS, and JavaScript. also optimize interfaces for seamless user interactions and stay updated with design trends and best practices to enhance user experiences.",
    profileImg: kartick,
  },
];

const OurTeam = () => {
  return (
    <section className="mb-5">
      <div className="row">
        <h3 className="text-center pb-5 text-uppercase">
          proudly introduce our development team
        </h3>
      </div>
      <div className="container">
        <Swiper
          modules={[Navigation, Scrollbar, A11y, EffectFade, Autoplay]}
          slidesPerView={1}
          spaceBetween={30}
          loop={true}
          autoplay={true}
          breakpoints={BreakPoints}
          className="mySwiper"
        >
          {ourTeams.map((ele, ind) => {
            const { name, profileImg, role, description } = ele;
            return (
              <SwiperSlide key={ind} className="py-2">
                <div className="swiper-card px-3 border-0 h-100 custom-shadow rounded-5 d-flex card flex-column justify-content-center align-items-center">
                  <div className="card-header border-0 bg-transparent">
                    <img
                      src={profileImg}
                      className="swiper-img d-block pb-5"
                      alt={"profile-img"}
                    />
                  </div>
                  <div className="card-body">
                    <h4>{name}</h4>
                    <p>
                      <strong>{role}</strong>
                    </p>
                    <p>{description}</p>
                  </div>
                </div>
              </SwiperSlide>
            );
          })}
        </Swiper>
      </div>
    </section>
  );
};

export default OurTeam;
